$main_color: #1EACC7;
$second-color: #ff5656;

@mixin btn_hover {
	&:hover {
		background-image: none;
		background-color: $main_color; } }
* {
    font-family: 'Roboto', sans-serif; }
header {
	position: relative;
	height: 50px; }
nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background: #212121;
	z-index: 2; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	.menu_link {
		font-weight: 300;
		font-size: 15px;
		line-height: 15px;
		color: #FFFFFF;
		text-decoration: none; }
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 50px; } }
.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 87px;
	background-color: rgba(0, 0, 0, .6);
	.subheader_logo {
		display: block;
		min-width: 100%;
		margin-top: 16px; }
	.subheader_official {
		margin-top: 6px;
		font-weight: 100;
		font-size: 13px;
		color: #FFFFFF;
		opacity: 0.6; } }
.subheader_call {
	margin-top: 19px;
	font-weight: 300;
	font-size: 12px;
	line-height: 14px;
	color: #FFFFFF;
	text-align: right; }
.subheader_phone {
	display: block;
	font-weight: 400;
	font-size: 22px;
	line-height: 35px;
	text-transform: uppercase;
	color: #FFFFFF;
	text-decoration: none;
	text-align: right; }
.subheader_btn {
	margin-top: 19px;
	width: 172px;
	height: 46px;
	border: none;
	background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
	box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
	border-radius: 4px;
	font-weight: 400;
	font-size: 14px;
	line-height: 36px;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF;
	text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
	@include btn_hover; }

.promo {
	min-height: 800px;
	background: url('../img/bg/uber_bg.jpg') center center/cover no-repeat;
	padding: 150px 0 135px 0;
	.promo_header {
		font-weight: 700;
		font-size: 60px;
		line-height: 24px;
		text-align: center;
		text-transform: uppercase;
		color: #FFFFFF;
		margin: 0;
		text-align: center; }
	.promo_subheader {
		margin: 0;
		margin-top: 22px;
		font-weight: 700;
		font-size: 36px;
		line-height: 48px;
		text-align: center;
		text-transform: uppercase;
		color: #FFFFFF;
		text-align: center; }
	.promo_descr {
		font-weight: 400;
		font-size: 18px;
		line-height: 24px;
		text-align: center;
		color: #FFFFFF;
		margin-top: 23px; }
	.promo_btn {
		display: block;
		width: 259px;
		height: 63px;
		margin: 118px auto 0 auto;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
		border-radius: 4px;
		font-weight: 400;
		font-size: 18px;
		line-height: 43px;
		text-align: center;
		text-transform: uppercase;
		border: none;
		color: #FFFFFF;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		@include btn_hover; } }

.reasons {
	padding: 71px 0 65px 0;
	.reasons_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px;
		.reasons_round {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 26px;
			width: 113px;
			height: 113px;
			background-color: $main_color;
			border-radius: 100%; }
		.reasons_descr {
			width: 343px;
			.reasons_subtitle {
				line-height: 21px;
				color: $main_color;
				font-size: 17px;
				font-weight: 400; }
			.reasons_text {
				color: #202020;
				font-size: 15px;
				font-weight: 300;
				line-height: 21px; } } } }
.label {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300 {
    text-align: center; }
	text-transform: uppercase; }

.title {
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center; }
.subtitle {
    margin-top: 19px;
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    text-align: center;
    color: #222222; }

.mobile {
    min-height: 864px;
    padding: 48px 0 80px 0;
    background: url("../img/bg/Подложка.jpg") center center/cover no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url("../img/myapp/Карточка 1.jpg") center center/cover no-repeat; }
        &_2 {
            background: url("../img/myapp/Карточка 2.jpg") center center/cover no-repeat; }
        &_3 {
            background: url("../img/myapp/Карточка 3.jpg") center center/cover no-repeat; }
        &_4 {
            background: url("../img/myapp/Карточка 4.jpg") center center/cover no-repeat; }
        &_5 {
            background: url("../img/myapp/Карточка 5.jpg") center center/cover no-repeat; }
        &_6 {
            background: url("../img/myapp/Карточка 6.jpg") center center/cover no-repeat; }
        &_subtitle {
            font-weight: 400;
            font-size: 24px;
            line-height: 31px;
            text-align: center;
            text-transform: uppercase;
            color: #FFFFFF; }
        &_plus {
            display: flex;
            margin-top: 20px; } } }

.choice {
	position: relative;
	min-height: 895px;
	padding: 57px 0 138px 0;
	background: url("../img/bg/Подложка.jpg") center center/cover no-repeat;
	z-index: -2;
	.black {
		position: absolute;
		top: 0;
		left: 0;
		height: 499px;
		width: 100%;
		background-color: #070716;
		z-index: -1; }
	.title, .title_white, .subtitle, .subtitle_white {
		color: white; }
	.label, .label_white {
		background-color: white;
		color: black;
		font-weight: 400; }
	.subtitle_white {
		margin-top: 0px; } }


.choice_img {
	position: relative;
	display: block;
	margin: 56px auto 0 auto; }
.choice_descr {
	text-align: center;
	color: #222222;
	font-size: 17px;
	font-weight: 300;
	line-height: 24px; }








